<template>
	<view class="read-list">
		<block v-for="(item, index) in readlist" :key="index">
		  <view class="item-book">
		    <view class="read-book" @click="gotoRead(item.flow_id, item.book_id)">
		      <text class="title">{{item.book_title}}</text>
		      <text>作者：{{item.pen_name}}</text>
		    </view>
		  </view>
		</block>
	</view>
</template>

<script>
import {mapGetters} from 'vuex'
	
export default {
	data() {
		return {
			readlist: []
		}
	},
	computed: {
		...mapGetters([
			'userId'
		])
	},
	onLoad(option) {
		this._initReadlist()
	},
	methods: {
		async _initReadlist() {
			let para = {
				user_id: this.userId,
				page: 1,
				rows: 100
			}
			let res = await this.$api.user.ReadBookList(para)
			if (res.code === 200) {
				this.readlist = res.data
			}
		},
		gotoRead(fId, bId) {
			uni.navigateTo({
				url: `../read/otherread?id=${fId}&bid=${bId}`
			})
		}
		
	}
}
</script>

<style lang="less" scoped>
	.read-list{
		width: 100%;
		.item-book{
			display: flex;
		  align-items: center;
		  margin: 0 30rpx;
		  padding: 20rpx 0;
		  border-bottom: 1rpx solid #E8E6E3;
		  .read-book{
				display: flex;
				flex-direction: column;
				width: 100%;
		    text{
		      font-size: 28rpx;
		    }
		    .title{
		      font-size: 34rpx;
		      color: #333;
		    }
		  }
		}
	}
</style>
